interface ApiResponse {
  success: boolean;
  data?: any;
  error?: string;
}

export async function callManusAI(payload: { command: string }): Promise<ApiResponse> {
  try {
    const response = await fetch('/api/manus-ai', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payload),
    });

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Error calling Manus AI:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error occurred'
    };
  }
}

export async function callBlackBoxAI(payload: { command: string }): Promise<ApiResponse> {
  try {
    const response = await fetch('/api/blackbox-ai', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payload),
    });

    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Error calling BlackBox AI:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error occurred'
    };
  }
}

export async function processCommand(command: string): Promise<ApiResponse> {
  try {
    // Route trading-related commands to Manus AI
    if (
      command.toLowerCase().includes('analisa') ||
      command.toLowerCase().includes('entry') ||
      command.toLowerCase().includes('posisi') ||
      command.toLowerCase().includes('trade') ||
      command.toLowerCase().includes('market')
    ) {
      return await callManusAI({ command });
    } else {
      // Route other commands to BlackBox AI
      return await callBlackBoxAI({ command });
    }
  } catch (error) {
    console.error('Error processing command:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error occurred'
    };
  }
}
