'use client';

import { useState, useEffect } from 'react';
import { useVoiceInput } from '@/hooks/useVoiceInput';
import { ASSISTANT_PROMPT } from '@/config/mulkyPrompt';
import { processCommand } from '@/lib/api';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Mic, MicOff, Send } from 'lucide-react';

interface Message {
  type: 'user' | 'assistant';
  content: string;
  timestamp: Date;
}

export default function AssistantPage() {
  const [command, setCommand] = useState('');
  const [messages, setMessages] = useState<Message[]>([
    {
      type: 'assistant',
      content: 'Hello! I am Mulky Assistant, your always-on AI assistant. How can I help you today?',
      timestamp: new Date()
    }
  ]);
  const [isProcessing, setIsProcessing] = useState(false);
  
  const {
    isListening,
    transcript,
    error,
    startListening,
    stopListening,
    clearTranscript
  } = useVoiceInput();

  useEffect(() => {
    if (transcript.trim()) {
      setCommand(transcript.trim());
    }
  }, [transcript]);

  const handleSend = async () => {
    if (!command.trim() || isProcessing) return;

    const userMessage: Message = {
      type: 'user',
      content: command,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setCommand('');
    clearTranscript();
    setIsProcessing(true);

    try {
      const response = await processCommand(command);
      
      // Add user message first
      const userMessage: Message = {
        type: 'user',
        content: command,
        timestamp: new Date()
      };
      setMessages(prev => [...prev, userMessage]);

      // Then add assistant response
      const assistantMessage: Message = {
        type: 'assistant',
        content: response.success && response.data?.message 
          ? response.data.message
          : response.error || 'Sorry, I encountered an error processing your command.',
        timestamp: new Date()
      };
      setMessages(prev => [...prev, assistantMessage]);
    } catch (error) {
      const errorMessage: Message = {
        type: 'assistant',
        content: 'Sorry, I encountered an unexpected error.',
        timestamp: new Date()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <div className="min-h-screen bg-white">
      <div className="max-w-4xl mx-auto p-4 sm:p-6 lg:p-8">
        {/* Header */}
        <header className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">Mulky Assistant</h1>
          <p className="text-lg text-gray-600">
            Your always-on, voice-activated AI assistant
          </p>
        </header>

        {/* Main Content */}
        <main className="space-y-6">
          {/* Messages Display */}
          <Card className="bg-gray-50">
            <CardContent className="p-4 h-[400px] overflow-y-auto space-y-4">
              {messages.map((message, index) => (
                <div
                  key={index}
                  className={`flex ${
                    message.type === 'user' ? 'justify-end' : 'justify-start'
                  }`}
                >
                  <div
                    className={`max-w-[80%] p-3 rounded-lg ${
                      message.type === 'user'
                        ? 'bg-black text-white ml-4'
                        : 'bg-gray-200 text-gray-900 mr-4'
                    }`}
                  >
                    <p className="text-sm">{message.content}</p>
                    <span className="text-xs opacity-70 mt-1 block">
                      {message.timestamp.toLocaleTimeString()}
                    </span>
                  </div>
                </div>
              ))}
            </CardContent>
          </Card>

          {/* Input Area */}
          <div className="flex items-center space-x-2">
            <Input
              type="text"
              placeholder="Type your command..."
              value={command}
              onChange={(e) => setCommand(e.target.value)}
              onKeyPress={handleKeyPress}
              className="flex-1"
              disabled={isProcessing}
            />
            <Button
              onClick={handleSend}
              disabled={!command.trim() || isProcessing}
              className="bg-black hover:bg-gray-800"
            >
              <Send className="h-4 w-4" />
            </Button>
            <Button
              onClick={isListening ? stopListening : startListening}
              variant={isListening ? "destructive" : "outline"}
              disabled={isProcessing}
            >
              {isListening ? (
                <MicOff className="h-4 w-4" />
              ) : (
                <Mic className="h-4 w-4" />
              )}
            </Button>
          </div>

          {/* Error Display */}
          {error && (
            <div className="text-red-500 text-sm mt-2">
              Error: {error}
            </div>
          )}

          {/* Processing Indicator */}
          {isProcessing && (
            <div className="text-gray-500 text-sm mt-2">
              Processing your command...
            </div>
          )}
        </main>
      </div>
    </div>
  );
}
